body {
  background: #000000;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#sol {
  text-decoration: none;
  font-size: 13px;
  position: relative;
  overflow: hidden;
  justify-content: center;
  border-radius: 5px;
  color: rgb(255, 255, 255);
  font-weight: bold;
 padding: 2px 2px;
 transition: 0.3s ease;
 margin-left: 2px;
}
#sol:hover {
  background-color: #ffffff;
  color: #000000;
} 
#line {
  margin-top: 25px;
  width: 40%;
}
#mint {
  color:#000000;
}
#mint:hover {
color: #000000;
}
#lin {
  color:#ffffff;
  width: 15%;
  margin-top: 50px;
}

#piede {
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
  overflow: hidden;
}
@media screen and (max-width: 1000px) {
  #thisball {
    width: 285px;
    height: 285px;
    margin-right: 10px;
  }
  #lin {
    width: 50%;
  }
}